
@import url('https://fonts.googleapis.com/css2?family=Montserrat&family=Roboto&family=Open+Sans&display=swap');

.App {
  text-align: center;
  padding: 0;
  font-family: var(--title-font);
  position: relative;

  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.App h1, h2, h3, h4, h5, h6 {
  font-family: var(--subtitle-font);
}

:root{
  /* Font Colors */
  --blue: #00ceff;
  /* Fonts */
  --title-font: "Open Sans", sans-serif;
  --subtitle-font: Montserrat, sans-serif;
}

@font-face {
  font-family: "Montserrat", sans-serif;
  src: url("https://fonts.googleapis.com/css2?family=Montserrat&display=swap");
}

@font-face {
  font-family: "Open Sans", sans-serif;
  src: url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@1,500&family=Open+Sans:wght@600&display=swap");
}

